.home{
    background: #fff;
}
.slider{
    margin-top: 45px;
}

.banner{
    background: #fff;
    ul{
        width: 100%;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        li{
            font-size: 12px;
            text-align: center;
            margin: auto;
            img{
                margin: 0 auto;
                width: 50%;
                height: 50%;
            }
        }
    }
}


.content{
    text-align: center;
    h3{
        width: 100%;
        height: 42px;
        line-height: 42px;
        background: #f3f3f3;
    }
    ul{
        height: 185px;
        background: #fff;
        display: flex;
        li{
            img{
                width: 100%;
            }
            p{
                margin: 8px 0;
                font-size: 13px;
            }
            p:nth-last-child(1){
                color: #e90404;
            }
        }
    }

}

.goods{
    height: 310px;
    font-size: 13px;
    margin-bottom: 20px;
    img{
        width: 100%;
        height: 105px;
    }
    div{
        width: 50%;
        height: 190px;
        padding-top: 15px;
        float: left;
        background-image: url("http://img.shop.wanmei.com/upload/moduleProduct/2017-07-20/566fb78abd7947699517250098628ab9.jpg");
        background-repeat: no-repeat;
        background-size: 80%;
        
    }
    ul{
        width: 50%;
        height: 205px;
        float: right;
        li{
            width: 100%;
            height:87.5px;
            padding-top: 15px;
            background-image: url("http://img.shop.wanmei.com/upload/moduleProduct/2017-07-20/39365988f6df4256a443172609bbb7db.jpg");
            background-size: 80%;
        }
        li:nth-last-child(1){
            background-image: url("http://img.shop.wanmei.com/upload/moduleProduct/2017-07-20/507d4d1d84b447f19a5d50794c74fc1f.jpg");
        }
    }
    p{
        margin: 5px 12px;
    }
    p:nth-last-child(1){
        color: #e90404;
    }
}

#top{
    position: fixed;
    right: 10px;
    bottom: 70px;
    display: block;
    width: 42px;
    height: 42px;
    background: url("http://static.wanmei.com/shop/mobile/images/gototop.png");
    background-size: 100%;
}

